#image{
    display: block;
    /* margin-top: 20px; */
    width:350px;
    /* color:  */
    border-radius: 10px;
    box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 5px rgb(238, 236, 236),
    2.3em 1.3em 1em rgba(0, 0, 0, 0.3);
}
#box #greeting{
    height: auto;
    width: auto;
}  
#heading{
    text-shadow: 5px 5px #c3c0bf;
    font-size: 55px;
}
#text1{
    text-shadow: 5px 5px #c3c0bf;
    font-size: 30px;
}
#text2{
    text-shadow: 5px 5px #c3c0bf;
    font-size: 30px;
}


@media only screen and (max-width: 1450px) {
    /* #homeMainBox{
        margin-top: 100px;
    } */
    #image{
        width: 320px;
    }  
    #greeting{
        height: auto;
        width: auto;
    }  
    #heading{
        font-size: 55px;
    }
    #text1{
        font-size: 30px;
    }
    #text2{
        font-size: 30px;
    }
}


@media only screen and (max-width: 1200px) {
    #image{
        width: 320px;
    }  
    #greeting{
        height: 320px;
        width: auto;
    }  
    #img1{
       width: 90px;
    }
    #img2{
        width: 90px;
    }
    #heading{
        font-size: 50px;
    }
    #text1{
        font-size: 30px;
    }
    #text2{
        font-size: 30px;
    }
}

