#menu{
      
    display: none;
}



@media only screen and (max-width: 1000px) {
    
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
   #defaultMenu .navLinks{
    font-size: 13.5px;
   }
   #heading{
    font-size: 25px;
   }
    
}
@media only screen and (max-width: 750px) {
    
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
    #defaultMenu{
      
        display: none;
    }
    #menu{
      
        display: initial;
    }
    
}

@media only screen and (max-width: 550px) {
    #heading{
        font-size: x-large;
    }
    #NavMainBox{
        width: 100vw;
        height: auto;
    }
    #container{
        height: auto;
    }
    #defaultMenu{
      
        display: none;
    }
    #menu{
      
        display: initial;
    }
    
}
