@media only screen and (max-width: 750px) {
    
    .MainBox{
        width: 100vw;
        height: auto;
        /* border: 5px solid black; */
    }
    #SkillsMainContainer{
        width: 100vw;
        height: auto;
        display: initial;
        /* border: 5px solid orange */

    }  
    #skillsBox{
        grid-template-columns: repeat(3, 1fr);
    }
    #toolsBox{
        grid-template-columns: repeat(3, 1fr);
    }
    .cards{
        margin: auto;
        margin-top: 30px;
        height:auto;
        width: 70%;
    }  
}
